import React from 'react';
import logo from "./logo.svg";

class Navigation extends React.Component {
     constructor(props) {
         super(props);
         this.state = {"navItems" : [
                 {
                     "name":"RAlpha",
                     "href":"home",
                     "logo": true
                 },
                 {
                     "name":"Conditions",
                     "href":"conditions",
                     "logo": false
                 },
                 {
                     "name":"Register",
                     "href":"register",
                     "logo": false
                 }
             ]
         };
    }

    render() {
        return (
            <div className="top-nav">
                <ul>
                    {
                        this.state.navItems.map( (nav, i) => {
                            let linkClass = "";
                            if (nav.logo) {
                                linkClass = "logo";
                            } else {
                                linkClass = "no";
                            }
                            return (<li key={i} className="item">
                                <a className={linkClass} href={nav.href}>{nav.name}</a></li>)
                        })
                    }
                    <li className="item"><a href="home"><img className="appLogo" src={logo} alt="logo" /></a></li>
                </ul>
            </div>);
    }
}

export default Navigation;



